<template>

<div class="uk-margin-top">
   <div class="uk-container uk-container-large">
      <!-- Form -->     
      <h2 class="uk-h3 uk-margin-small-top">Profile</h2>
      
      <form class="uk-form-stacked uk-text-small" >
         <div id="fullname" class="uk-child-width-1-2@s uk-child-width-1-3@m" uk-grid >
            <div class="uk-first-column">
               <div class="uk-margins">
                  <label class="uk-form-label uk-text-bold" for="form-stacked-text" v-bind:class="{ 'uk-form-danger' : false }" uk-tooltip="title: Required; pos: left">First name</label>
                  <div class="uk-form-controls">
                     <input name="first" class="uk-input" type="text" v-model="user.firstName">
                  </div>
               </div>
            </div>
            <div class="uk-first-column">
               <div class="uk-margins">
                  <label class="uk-form-label uk-text-bold" for="form-stacked-text" v-bind:class="{ 'uk-form-danger' : false }" uk-tooltip="title: Required; pos: left">Middle name</label>
                  <div class="uk-form-controls">
                     <input name="middle" class="uk-input" type="text" v-model="user.middleName">
                  </div>
               </div>
            </div>
            <div class="uk-first-column">
               <div class="uk-margins">
                  <label class="uk-form-label uk-text-bold" for="form-stacked-text" v-bind:class="{ 'uk-form-danger' : false }" uk-tooltip="title: Required; pos: left">Last name</label>
                  <div class="uk-form-controls">
                     <input name="last" class="uk-input" type="text" v-model="user.lastName">
                  </div>
               </div>
            </div>
         </div>
         <div class="uk-child-width-1-2@s uk-child-width-1-3@m" uk-grid>
            <div class="uk-first-column">
               <div class="uk-margins">
                  <label class="uk-form-label uk-text-bold" for="form-stacked-text">Date of Birth</label>
                  <div class="uk-form-controls">
                     <input class="uk-input" type="date" placeholder="" v-model="user.dateofbirth">
                  </div>
               </div>
            </div>
         </div>
         <div class="uk-child-width-1-2@s uk-child-width-1-3@m" uk-grid>
            <div class="uk-margins" v-if="countries">
               <label class="uk-form-label uk-text-bold" for="form-stacked-text">Country</label>
               <div uk-form-custom="target: true" class="uk-form-custom">
                  <select>
                        <option v-for="(country,index) in countries" :value="country" :key="index">{{country}}</option>
                  </select>
                  <span class="uk-select uk-form-width-medium">Please select...</span>
               </div>
            </div>
            <div class="uk-first-column">
               <div class="uk-margins">
                  <label class="uk-form-label uk-text-bold" for="form-stacked-text">City/Province</label>
                  <div class="uk-form-controls">
                     <input class="uk-input" type="text" v-model="user.city">
                  </div>
               </div>
            </div>
            <div class="uk-first-column">
               <div class="uk-margins">
                  <label class="uk-form-label uk-text-bold" for="form-stacked-text">Street/House No.</label>
                  <div class="uk-form-controls">
                     <input class="uk-input" type="text" v-model="user.street">
                  </div>
               </div>
            </div>
         </div>
         <div class="uk-child-width-1-2@s uk-child-width-1-3@m" uk-grid>
            <div class="uk-first-column">
               <div class="uk-margins">
                  <label class="uk-form-label uk-text-bold" for="form-stacked-text" uk-tooltip="title: FB/Twitter/Linked-In; pos: right">Profile Link</label>
                  <div class="uk-form-controls">
                     <input class="uk-input" type="text" placeholder="Optional" v-model="user.profile_link">
                  </div>
               </div>
            </div>
            <div class="uk-margins">
               <div class="uk-form-label uk-text-bold">Gender</div>
               <div class="uk-form-controls">
                  <label><input class="uk-radio" type="radio" name="radio1"> Male</label>&nbsp;&nbsp;
                  <label><input class="uk-radio" type="radio" name="radio1"> Female</label>&nbsp;&nbsp;
                  <label><input class="uk-radio" type="radio" name="radio1"> Rather not say</label>
               </div>
            </div>
         </div>
         
         <div class="uk-margin-large-bottom"><br></div>
         <div class="footer-buttons">
            <div class="footer-padding">
               <button class="uk-button uk-button-primary uk-icon uk-align-right uk-margin-remove-bottom" @click.prevent="checkInputs"><span uk-icon="icon: check;" class="uk-icon"></span> Save </button>
            </div>
         </div>
      </form>
      
      <!-- Form -->
   </div>
</div>

</template>

<script>
import axios from 'axios'

axios.defaults.baseURL = 'http://127.0.0.1:8000/api'
axios.defaults.headers.common['header'] = 'application/json'

export default {

   data() {
      return {

         isEmptyFn: false,
         isEmptyMn: false,
         isEmptyLn: false,

         user: {
            firstName: '',
            middleName: '',
            lastName: '',
            dateofbirth: '',
            city: '',
            street: '',
            gender: '',
            country: '',
            profileLink: '',
         },
         countries: {},


      }
   },

   created() {
      this.getProfile()
   },

   
   computed: {
      
   },

   methods: {
      getProfile() {
         if(this.$store.state.token) {
            return new Promise((resolve,reject) => { 
               axios.get('/user/profile', {
                  headers: {
                     Authorization: 'Bearer '+this.$store.state.token,
                  },               
               })
               .then(response => {
                  // user details
                  const {data:{data}} = response
                  this.user.firstName = data.firstName
                  this.user.middleName = data.middleName
                  this.user.lastName = data.lastName
                  this.user.dateofbirth = data.dateofbirth
                  this.user.city = data.city
                  this.user.street = data.street
                  this.user.gender = data.gender
                  this.user.country = data.country
                  this.user.profileLink = data.profileLink
                  
                  this.countries = response.data.country
                  // console.log(this.countries)
                  resolve(response) 
               })
               .catch( error => {

                  // console.log(error)
                  reject(error)
               })
            })
         }
      },

      checkInputs() {
        
      }
   }
}
</script>

